import React from 'react';
import { NavLink } from 'react-router-dom';

function Nav() {
  return (
    <nav>
      <div className="desc"><a href="http://merogers.dev" target="_blank" rel="noopener noreferrer">merogers.dev</a></div>
      <ul>
        <li>
          <NavLink 
          exact={ true } 
          to="/" 
          activeClassName="current">
            Home
          </NavLink>
        </li>
        <li>
          <NavLink
            exact={ true } 
            to="/portfolio"
            activeClassName="current">
              Portfolio
          </NavLink>
        </li>
        <li>
          <NavLink 
            exact={ true }
            to="/contact"
            activeClassName="current">
              Contact
          </NavLink>
        </li>
      </ul>
    </nav>
  );
}

export default Nav;